import { combineReducers } from 'redux'


// App reducer
const initialState = {
    currentApp: 'Chilli Hot',
    appsList:{
			apps:['Chilli Hot', 'Tester', 'Contact', 'reddit', 'app3', 'Shopping'], 
			"Chilli Hot": {color:'red', navItems:['n1', 'n2', '3ccc', {icon:'menu'}, {icon:'shopping_cart'}, {icon:'search'}]},
			"reddit": {color:'blue', navItems:[{icon:'menu'}, {icon:'search'}]},
			"app3": {color:'green', navItems:[{icon:'menu'}]},
			"Shopping": {color:'yellow', navItems:[{icon:'menu'}, {icon:'shopping_cart'}]},
			"Contact": {color:'grey', navItems:[ {icon:'menu'},{icon:'email'}, {icon:'phone'} ]},
			"Tester": {color:'black', navItems:[ {icon:'menu'} ]},
		},
    current: 0
};
   
const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'CHANGE_APP_STATE':
            const nextAppIndex = state.current === state.appsList.apps.length - 1 ? 0 : state.current + 1
            const nextApp = action.payload ? action.payload : state.appsList.apps[nextAppIndex]
            return {
                ...state,
                currentApp: nextApp,
                current:nextAppIndex
            };
        default:
        return state;
    }
};


// SearchbarComponent reducer
const initialSearchState = {opened:false, currentSearchTerm:''}
const searchReducer = (state = initialSearchState, action) => {
	switch(action.type){
		case 'TOGGLE_SEARCHBAR':
			const opened = !state.opened
			return {opened, currentSearchTerm:state.currentSearchTerm }
		case 'CLOSE_SEARCHBAR':
			return {...state, opened:false}
		case 'OPEN_SEARCHBAR':
			return {...state, opened:true}
		case 'SEARCH_TERM_SELECTED':
		   return {opened:state.opened, currentSearchTerm:action.payload }
		default:
			return state	 		
	}
}


const rootReducer = combineReducers({
	//auth: authReducer,
	//project: projectReducer,
	//nav: navReducer,
	app: appReducer,
	//app2: app2Reducer,
	searchbar: searchReducer,
	//cartOpen:cartReducer
});

export default rootReducer
