import React from 'react';
import { Link } from 'react-router-dom';
import { NavLink } from 'react-router-dom';
import { SearchbarComponent } from './SearchbarComponent'
import { useSelector, useDispatch, useStore } from 'react-redux';

const Navbar = () => {
  const dispatch = useDispatch();
  const store = useStore();
  
  const App = useSelector(state => state.app);
  const currentApp = App.currentApp;
  const storeNavItems = App.appsList[currentApp].navItems; 

  const navMenuAction = (data, currentApp) => { 
		return { type:'CHANGE_APP_STATE', payload: null }
	}
  const showSearchBar = () => {	dispatch(toggleSearchBar())	}	
  const toggleCartAction = () => { return { type:'TOGGLE_CARTOPEN' } }
  const toggleSearchBar = () => { return { type:'TOGGLE_SEARCHBAR' } }
  const closeSearchBar = () => {return { type:'CLOSE_SEARCHBAR' } }
  const openSearchBar = () => { return { type: null } }
  const addMenuBtn = (item, currentApp) => {
    // Close searchbar unless next item is reddit
    const appsArray = App.appsList.apps;
		if(appsArray[appsArray.indexOf(currentApp) + 1] !== 'reddit'){
			dispatch(closeSearchBar())
		} else {
			dispatch(openSearchBar())
		}
		dispatch(navMenuAction(item, currentApp))
		console.log('STORE:',store.getState());
	}  
  const iconButton = (fn, icon, index) => { 
    return (
      <span key={index} className="right white-text"  onClick={ fn }  >
        <i className="material-icons waves-effect  waves-light">{ icon }</i>
      </span>
    )
  }
  const navItems = storeNavItems && storeNavItems.map((navItem, i) => {
    if ((typeof navItem) === 'object'){
      const {icon} = navItem
      let fn
      switch(icon){
        case 'menu':
          fn = addMenuBtn
          break
        case 'shopping_cart':
          fn = toggleCartAction
          break
        case 'search':
          fn = showSearchBar
          break
        default:
          fn = null
        }
      return  iconButton(fn, icon, i)
    } 
    return <li key={i}><NavLink to="/">{ navItem }</NavLink></li>
  });
	const color = App.appsList[currentApp].color; 
	const className = `nav-wrapper ${color} darken-3`
  
  // Return the component.
  return(
    <section id='Navbar'>
      <nav className={ className } >
        <div className="container">
          <Link className="brand-logo" to="/">{ currentApp }</Link>
          <ul className="right">
            { navItems }
          </ul>
      
        </div>
      </nav> 
      <SearchbarComponent />
    </section>
    )	
}

export default Navbar




